.modal{
    display: none;
        position: fixed;
        z-index:1;
        overflow: auto;
        left: 0;
        top:0;
        width: 100%;
        height:100%;
        background: rgba(0, 0, 0, 0.452);

  .flex{
        width: 100%;
        height:100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2{
            font-size: 1.4rem;
        }
    }

    .textos{
        padding:300px;
        color:#fff;
        text-align: center;
    }

    h1{
        font-size: 40px;
        margin-bottom:20px;
    }

    a{
        text-decoration: none;
        font-size:20px;
        display: inline-block;
        padding: 7px;
        width: 220px;
        border:1px solid #fff;
        border-radius: 10px;
        color:#fff;
    }
    a:hover{background: #000; color:#fff; border:1px solid #000;}

    .contenido-modal{
        position: relative;
        background-color: #fefefe;
        margin: auto;
        width: 30%;
        box-shadow: 0 0 6px 0 rgba(0, 0, 0, .4);
        animation-name: modal;
        animation-duration: 1s;
    }
    @keyframes modal{
        from{top:-330px; opacity:0;}
        to{top:0; opacity:1;}
    }
    .close{
        color: #f2f2f2;
        font-size:30px;
        font-weight: bold;
    }
    .close:hover{
        color:#7f8c8d;
        text-decoration: none;
        cursor: pointer;
    }

    .modal-header, .footer{
        padding: 0 8px;
        background: #34495e;
        color:#f2f2f2;
    }

    p{
        text-align: justify;
    }

    .modal-body{
        padding: 20px 16px;
    }
}


@media screen and (max-width:900px){
	.contenido-modal{
		width: 70%;
	}
	.textos{
		padding: 150px;
	}
}

@media screen and (max-width:500px){
	.textos{
		padding:50px;
	}
}